.header__menu-button {
  display: none; }

.header__menu-toggle {
  width: 30px;
  height: 30px;
  border: none;
  padding: 0;
  background-color: transparent;
  cursor: pointer;
  transition-duration: 200ms;
  outline: none; }

.header__menu-toggle-bar {
  position: relative;
  display: block;
  width: 30px;
  height: 2px;
  background-color: white;
  transition-duration: 200ms; }

.header__menu-toggle-bar::before {
  content: "";
  position: absolute;
  width: 30px;
  height: 2px;
  top: -8px;
  left: 0;
  background-color: white;
  transition-duration: 200ms; }

.header__menu-toggle-bar::after {
  content: "";
  position: absolute;
  width: 30px;
  height: 2px;
  top: 8px;
  left: 0;
  background-color: white;
  transition-duration: 200ms; }

.header__menu-toggle-bar--active {
  transform: rotate(45deg); }
  .header__menu-toggle-bar--active::after {
    display: none; }
  .header__menu-toggle-bar--active::before {
    transform: translateY(7px) rotate(90deg); }

.header__hamburger {
